.banking-container {
  .actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    .title {
      font-size: 24px;
      font-weight: 600;
    }
    .buttons {
      display: flex;
      align-items: center;
      gap: 14px;
      .ant-btn {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
  .metrics {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    .card {
      background: #fff;
      padding: 10px 14px;
      border-radius: 12px;
      min-width: 23%;
      .title {
        font-size: 14px;
        border-bottom: 1px dashed #20b75761;
        padding-bottom: 10px;
        margin-bottom: 10px;
        font-weight: 500;
      }
      .data {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .value {
          .number {
            font-size: 18px;
            font-weight: 500;
          }
          .desc {
            margin-top: 4px;
            color: #20b757;
            font-size: 14px;
          }
        }
        .graph {
          font-size: 40px;
          color: #20b757;
          height: 54px;
        }
      }
    }
  }
  .table-container {
    background: #fff;
    padding: 20px 14px;
    .filters {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
      border-bottom: 1px dashed #20b75761;
      padding-bottom: 10px;

      .title {
        font-size: 18px;
        font-weight: 600;
      }
      .buttons {
        display: flex;
        gap: 10px;

        .ant-radio-button-wrapper:last-child {
          border-start-end-radius: 30px;
          border-end-end-radius: 30px;
        }
        .ant-radio-button-wrapper:first-child {
          border-start-start-radius: 30px;
          border-end-start-radius: 30px;
          border-inline-start: none;
        }
        .ant-radio-button-wrapper-checked {
          border-start-end-radius: 30px;
          border-end-end-radius: 30px;
          border-start-start-radius: 30px;
          border-end-start-radius: 30px;
          background: #20b757 !important;
        }
        .ant-radio-button-wrapper-checked:not(
            .ant-radio-button-wrapper-disabled
          )::before,
        .ant-radio-button-wrapper-checked:not(
            .ant-radio-button-wrapper-disabled
          ):hover::before {
          background-color: transparent;
        }

        .ant-radio-group {
          background: #20b75715 !important;
          border-radius: 32px;
          border: 1px solid #ebecef;
        }

        .ant-radio-button-wrapper {
          border: none;
          background: transparent;
          font-weight: 500;
          line-height: 32px;
          font-size: 12px;
        }
        .ant-radio-button-wrapper:not(:first-child)::before {
          display: none;
        }

        .ant-input-outlined {
          padding-right: 4px;
          background: #20b75715 !important;
          font-size: 12px;
          button {
            font-size: 12px;
          }
        }
      }
    }

    .ag-row.ag-full-width-row.ag-row-group > * {
      font-weight: 600;
    }
    .ag-header-cell-label {
      font-weight: 600;
    }
    .ag-root-wrapper {
      border: none;
    }
    .ag-row {
      border-bottom: 1px solid #ebecef;
    }
    .ag-row-hover {
      background: #20b75715 !important;
    }

    .ag-cell.ag-cell-not-inline-editing.ag-cell-normal-height.ag-cell-value {
      align-items: center;
      display: flex;
      justify-content: flex-start;
    }
    .account-cell {
      display: flex;
      align-items: center;
      gap: 10px;
      padding-top: 4px;
      .icon {
        height: 30px;
        width: 30px;
        background: #205cb7;
        text-transform: capitalize;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 22px;
        line-height: 20px;
      }
      .content {
        line-height: 20px;

        .main {
        }
        .meta {
          font-size: 12px;
          opacity: 0.5;
          font-weight: 400;
        }
      }
    }
    .category-cell {
      width: 100%;
      .ant-select {
        width: 100%;
      }
      .ant-select-selector {
        font-size: 14px;
        border-radius: 32px;
      }
    }
  }
  .pagination {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .meta {
    }
    .actions {
    }
  }
}

::ng-deep {
  .buttons {
    .ant-btn {
      font-size: 14px;
    }
  }
  .ant-radio-button-wrapper:last-child {
    border-start-end-radius: 30px;
    border-end-end-radius: 30px;
  }
}
